<script>
import axios from 'axios'
export default {
  components: {},
  data() {
    return {
      data: {},
      crime: ['', '', ' ', ''],
      living: ['', '', '', ' '],
      rank: { h: 'r1', t: 'r2', l: 'r3', n: 'r4', m: 'r5' },
      category: {
        r1: '',
        r2: '',
        r3: '',
        r4: '',
        r5: ''
      },
      dorm: ['', '', '1 !'],
      exi: ['!', '!'],
      poss: ['!', '!'],
      uniq: {
        A: '    ',
        B: '   ',
        C: '      ',
        D: '      / TEST '
      },
      u: [],
      coll: '',
      univ_name: '',
      univlist: []
    }
  },
  setup() {},
  create() {},
  mounted() {
    this.univ_name = this.$route.query.name
    this.getData()
    if(!sessionStorage.getItem('college')){
      this.coll = this.rank['t']
    }else{
      this.coll = sessionStorage.getItem('college')
    }
    // try{
    //   this.coll = sessionStorage.getItem('college')
    // }
    // catch {
    //   this.coll = this.rank['t']}
    // this.u = this.data.uniq
  },
  unmounted() {},
  methods: {
    async getData() {
      try {
        const res = await axios.get('/api/univ/', {
          params: { name: this.univ_name }
        })
        this.data = res.data
      } catch (err) {
        console.error(err)
      }
    },
    loading() {
      this.isLoading = false
    }
  },
  computed: {
    gvi_idx() {
      return Math.max((24 * (this.data.gvi_idx - 50)) / 5, 0)
    },
    sdg_idx() {
      return Math.max((24 * (this.data.sdg_idx - 50)) / 5, 0)
    }
  }
}
</script>
