          :class="{
            'detailsearch_button transportation': !detailsearch_clicked[1],
            'detailsearch_button transportation clicked':
              detailsearch_clicked[1]
          }"
          @click="transportation"
        >
           
        </button>
      </div>
      <div class="detailsearch_dropdown_box">
