<script>
import axios from 'axios'
export default {
  data() {
    return {
      search_name: '',
      continent_name: [],
      continent_clicked: [false, false, false, false, false],
      weather_options: [
        { value: 'hothumid', text: '  ' },
        { value: 'warmhumid', text: '  ' },
        { value: 'coolhumid', text: '  ' },
        { value: 'coldhumid', text: '  ' },
        { value: 'hotdry', text: '  ' },
        { value: 'warmdry', text: '  ' },
        { value: 'cooldry', text: '  ' },
        { value: 'colddry', text: '  ' }
      ],
      language_options: [
        { value: 'english', text: '' },
        { value: 'french', text: '' },
        { value: 'spanish', text: '' },
        { value: 'chinese', text: '' },
        { value: 'japanese', text: '' }
      ],
      dormitory_options: [
        { value: 'per_domi', text: '1 ' },
        { value: 'domi1', text: '' },
        { value: 'domi0', text: '' }
      ],
      weather: '',
      language: '',
      dormitory: '',
      detailsearch_clicked: [false, false, false],
      selected_univ: [],
      number: 0,
      seperated_univ: [],
      seperated_univ2: [[], [], [], [], [], [], [], []],
      page: [false, false, false, false],
      present_page: 1
    }
  },
  setup() {},
  create() {},
  mounted() {
    this.getData(
      'https://9fe2a3ed-7549-4f88-914b-6406f5d56602.mock.pstmn.io/mocklist/list3'
    )
  },
  unmounted() {},
  computed: {
    heightchange() {
      if (this.seperated_univ2[this.present_page - 1].length >= 4) {
        return 1415
      } else if (this.seperated_univ2[this.present_page - 1].length >= 3) {
        return 1060
      } else if (this.seperated_univ2[this.present_page - 1].length >= 2) {
        return 705
      } else if (this.seperated_univ2[this.present_page - 1].length >= 1) {
        return 355
      } else {
        return 20
      }
    },
    total_page() {
      if (this.number > 84) {
        return 8
      } else if (this.number > 72) {
        return 7
      } else if (this.number > 60) {
        return 6
      } else if (this.number > 48) {
        return 5
      } else if (this.number > 36) {
        return 4
      } else if (this.number > 24) {
        return 3
      } else if (this.number > 12) {
        return 2
      } else if (this.number > 0) {
        return 1
      } else {
        return 0
      }
    }
  },
  methods: {
    search() {
      this.getData(
        'https://9fe2a3ed-7549-4f88-914b-6406f5d56602.mock.pstmn.io/mocklist/list4'
      )
      this.present_page = 1
    },
    continentclick() {
      if (event.target.value === 'northamerica') {
        if (this.continent_clicked[0] === false) {
          this.continent_clicked[0] = true
          this.continent_name.push(event.target.value)
        } else {
          this.continent_clicked[0] = false
          this.continent_name = this.continent_name.filter(
            (element) => element !== event.target.value
          )
        }
      } else if (event.target.value === 'europe') {
        if (this.continent_clicked[1] === false) {
          this.continent_clicked[1] = true
          this.continent_name.push(event.target.value)
        } else {
          this.continent_clicked[1] = false
          this.continent_name = this.continent_name.filter(
            (element) => element !== event.target.value
          )
        }
      } else if (event.target.value === 'asia') {
        if (this.continent_clicked[2] === false) {
          this.continent_clicked[2] = true
          this.continent_name.push(event.target.value)
        } else {
          this.continent_clicked[2] = false
          this.continent_name = this.continent_name.filter(
            (element) => element !== event.target.value
          )
        }
      } else if (event.target.value === 'oceania') {
        if (this.continent_clicked[3] === false) {
          this.continent_clicked[3] = true
          this.continent_name.push(event.target.value)
        } else {
          this.continent_clicked[3] = false
          this.continent_name = this.continent_name.filter(
            (element) => element !== event.target.value
          )
        }
      } else if (event.target.value === 'southamerica') {
        if (this.continent_clicked[4] === false) {
          this.continent_clicked[4] = true
          this.continent_name.push(event.target.value)
        } else {
          this.continent_clicked[4] = false
          this.continent_name = this.continent_name.filter(
            (element) => element !== event.target.value
          )
        }
      }
    },
    rankingclick() {
      this.detailsearch_clicked[0] = !this.detailsearch_clicked[0]
    },
    transportationclick() {
      this.detailsearch_clicked[1] = !this.detailsearch_clicked[1]
    },
    moneyclick() {
      this.detailsearch_clicked[2] = !this.detailsearch_clicked[2]
    },
    ranking() {
      this.detailsearch_clicked[0] = !this.detailsearch_clicked[0]
    },
    transportation() {
      this.detailsearch_clicked[1] = !this.detailsearch_clicked[1]
    },
    money() {
      this.detailsearch_clicked[2] = !this.detailsearch_clicked[2]
    },
    case1() {
      this.getData(
        'https://9fe2a3ed-7549-4f88-914b-6406f5d56602.mock.pstmn.io/mocklist/list1'
      )
      this.present_page = 1
    },
    case2() {
      this.getData(
        'https://9fe2a3ed-7549-4f88-914b-6406f5d56602.mock.pstmn.io/mocklist/list2'
      )
      this.present_page = 1
    },
    async getData(url) {
      try {
        this.selected_univ = await this.api(url, 'get')
        this.number = this.selected_univ.length
        for (let i = 0; i < Math.ceil(this.number / 3); i++) {
          this.seperated_univ.push(this.selected_univ.slice(0, 3))
          this.selected_univ.splice(0, 3)
        }
        for (let j = 0; j < Math.ceil(this.number / 12); j++) {
          this.seperated_univ2[j] = this.seperated_univ.slice(0, 4)
          this.seperated_univ.splice(0, 4)
        }
      } catch (err) {
        console.error(err)
      }
    },
    async api(url, method, data) {
      return (
        await axios({
          method: method,
          url: url,
          data: data
        }).catch((e) => {
          console.log(e)
        })
      ).data
    },
    page_up() {
      if (this.present_page < this.total_page) {
        this.present_page += 1
      }
    },
    page_down() {
      if (this.present_page !== 1) {
        this.present_page -= 1
      }
    },
    detailresult(event) {
      this.$router.push({
        name: 'detailresult',
        query: { name: event.target.value }
      })
    }
  }
}
</script>
